import React, { useEffect, useState } from 'react';
import {View, Image, ImageBackground, TouchableOpacity, Text, Animated} from 'react-native';

import {uri, setDecryptedToken, setDecryptedUsername, setGlobalInstitutionDetails,
     getDecryptedToken, getDecryptedUsername, setLogoUri, setGLogoUrl} from '../Globals/';

import {connect} from 'react-redux';


import Logo from '../../assets/images/trainee_ar.png';
import Logout from '../../assets/icons/logout.png';
import NotificationBell from '../../assets/icons/notification_bell.png';
import NotificationBellCircle from '../../assets/icons/notification_info_circle.png';

import headerStyles from '../../Styles/Header/style';

import Cookies from 'universal-cookie/es6';

import {fetchNotifications, broadcastNewInstitutionDetails} from '../actions/index';
import {WaitingModal} from '../Modals/WaitingModal';
import {setG_API_KEY} from '../Globals';

function DashboardHeader(props){


    const cookie                                       = new Cookies();

    //strings
    const [institution_details, setInstitutionDetails] = useState("");
    const [name, setName]                              = useState("");
    const [phone, setPhone]                            = useState("");
    const [email, setEmail]                            = useState("");
    const [logo, setLogo]                              = useState("");
    const [logoUrl, setLogoUrl]                        = useState("");
    const [location, setLocation]                      = useState("");
    const [map_location, setMap_location]              = useState();
    const [domainType, setDomainType]                  = useState("");
    const [description, setDescription]                = useState("");
    const [taxID, setTaxID]                            = useState("");

    const [branches, setBranches]                      = useState();
    const [orders, setOrders]                          = useState();

    const [notifications, setNotifications]            = useState();
    const [countNotifica, setCountNotifica]            = useState(0);
    const [logs, setLogs]                              = useState();
    
    const [runFetchOnce, setRunFetchOnce]              = useState(true);
    const [firstLoad, setFirstLoad]                    = useState(true);
    
    var notificationOpacity = new Animated.Value(0);


    const updateInstitutionDetails = async () =>{
        var data = await JSON.stringify(props.state.data);
        if(data !== institution_details && props.state.data !== undefined ){
            setGlobalInstitutionDetails(await JSON.stringify(props.state.data));

            setName(props.state.data.name);
            setPhone(props.state.data.phone);
            setEmail(props.state.data.email);
            
            if(props.state.data.logo !== null){
                setLogo(props.state.data.logo);
                setLogoUri(props.state.data.logo);
            }

            setLocation(props.state.data.location);
            setMap_location(props.state.data.map_location);
            setDomainType(props.state.data.domainType);
            setDescription(props.state.data.description);
            setTaxID(props.state.data.taxID);

            setBranches(props.state.data.branches);
            setOrders(props.state.data.orders);
            setNotifications(props.state.data.notifications);
            setLogs(props.state.data.logs);
            
            setG_API_KEY(props.state.data.G_API_KEY);

        }
    }

    const countUnseenNotifications = () => {
        if(notifications !== undefined) {
            let counter = 0;
            notifications.map((notification) =>{
                if(notification.seen === false) {
                    counter++;
                }
            })
            setCountNotifica(counter);
        }
    }
    

    const runFetchInstitutionDetails = () => {
        if(runFetchOnce){
            setRunFetchOnce(false);
            setTimeout(() => {
                fetchInstitutionDetails(getDecryptedUsername(), getDecryptedToken()).then(()=>{
                    if(firstLoad){
                        setFirstLoad(false);
                    }
                });
            }, 5000);
        }
    }

    const playNotificationAnimation = () => {
        Animated.loop(
            Animated.sequence([
                Animated.timing(notificationOpacity, {
                    toValue: 0.1,
                    duration: 500,
                    useNativeDriver: false
                }),
                Animated.timing(notificationOpacity, {
                    toValue: 0.4,
                    duration: 500,
                    useNativeDriver: false
                }),
                Animated.timing(notificationOpacity, {
                    toValue: 0.7,
                    duration: 500,
                    useNativeDriver: false
                }),
                Animated.timing(notificationOpacity, {
                    toValue: 1,
                    duration: 500,
                    useNativeDriver: false
                }),
                Animated.timing(notificationOpacity, {
                    toValue: 0.7,
                    duration: 500,
                    useNativeDriver: false
                }),
                Animated.timing(notificationOpacity, {
                    toValue: 0.4,
                    duration: 500,
                    useNativeDriver: false
                }),
            ])
        ).start();
    }

    useEffect(() => {
        updateInstitutionDetails();
        countUnseenNotifications();
        runFetchInstitutionDetails();
        playNotificationAnimation();
    });


    const logout = () => {
        cookie.remove('trainee-cookie');
        cookie.set('TraineeWelcomeMessage', true);
        setDecryptedToken("");
        setDecryptedUsername("");
        props.setPath('Login');
    }

    const goToNotifications = () => {
        props.setPath('Notifications');
    }

    const fetchInstitutionDetails = async (username, token) => {
        let data          = [];

    
        var headerData = new Headers();

        headerData.append("Authorization", `Token ${token}`);

        var requestOptions = {
            method: 'GET',
            headers: headerData,
            redirect: 'follow'
        };

        const response = await fetch(`${uri}/api/institution/profile/${username}/`, requestOptions)
        .then( async (response) => {
            if(response.status === 200){
                let parsed_data = await response.json();

                data = parsed_data;
                setName(data.name);
                // setPhone(props.state.data.phone);
                setEmail(data.email);
                //set to globals

                if(data.logo !== null){
                    setLogo(data.logo);
                    setLogoUri(data.logo);

                    // setLogoUrl(data.logoUrl);
                    // setGLogoUrl(data.logoUrl);        
                }
                // else{
                //     setLogo(data.logo);
                //     setLogoUri(data.logo);
                // }
    
                // setLocation(props.state.data.location);
                // setMap_location(props.state.data.map_location);
                // setDomainType(props.state.data.domainType);
                // setDescription(props.state.data.description);
                // setTaxID(props.state.data.taxID);
    
                // setBranches(props.state.data.branches);
                // setOrders(props.state.data.orders);
                setNotifications(data.notifications);
                setRunFetchOnce(true);
                setLogs(data.logs);

                setG_API_KEY(data.G_API_KEY);


                setInstitutionDetails(await JSON.stringify(props.state.data));
                props.broadcastNewInstitutionDetails(data);
                
                // data["responseMsg"] = "";
            }else if(response.status === 401){
                data["responseMsg"] = "خـطأ في إسـم الـمستخدم أو كـلمة المرور";
            }
        }).catch((error) => {
            setRunFetchOnce(true);
            console.log('error', error);
        });
    
    }
    

    return(
        
        <View style={headerStyles.backRect}>
            
            {
                firstLoad === true ?
                    <WaitingModal />
                :
                    null
            }

            <View style={headerStyles.frontRectDashboard} >
                <View style={headerStyles.frontRectDashboardContainer} >
                    {/* logout & notifications */}
                    <View style={headerStyles.Logout_Bell}>

                        <TouchableOpacity onPress={()=>{logout()}} >
                            <Image source={Logout} style={headerStyles.logout} />
                        </TouchableOpacity>

                        {
                             props.getPath() !== "Notifications"  ?
                               props.getPath() !== "NotificationDetails" ?
                                    <TouchableOpacity onPress={()=>goToNotifications()}>
                                        <ImageBackground source={NotificationBell} style={headerStyles.logout}>
                                            {
                                                countNotifica > 0 ?
                                                    <Animated.View style={{opacity: notificationOpacity}}>
                                                            <ImageBackground source={NotificationBellCircle} style={[headerStyles.notification_circle,]}>
                                                                <Text style={headerStyles.notification_circle_text}>{countNotifica}</Text>
                                                            </ImageBackground>
                                                    </Animated.View>
                                                :
                                                    null
                                            }
                                        </ImageBackground>
                                    </TouchableOpacity>
                                :
                                    null
                            :
                                null
                        }



                    </View>


                    <View style={{justifyContent: 'center'}}>
                        <Image source={Logo} style={headerStyles.logo} />
                    </View>

                    <View style={headerStyles.institlogo_name}>
                        <Text style={headerStyles.institution_name}>{name}</Text>
                        {
                            logoUrl !== "" ?
                                    <Image source={logoUrl} style={headerStyles.institution_logo} />
                                :
                                    logo !== "" ?
                                        <Image source={`${uri}${logo}`} style={headerStyles.institution_logo} />
                                    :
                                        null
                        }
                    </View>


                </View>
            </View>
        </View>

    );

}



const mapStateToProps = (state) => {
    return{
        state:{
            data: state.broadcastNewInstitutionDetails.data
        }
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        fetchNotifications: (username, token)=>{
            dispatch(fetchNotifications(username, token))
        },
        broadcastNewInstitutionDetails: (data)=>{
            dispatch(broadcastNewInstitutionDetails(data))
        }
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(DashboardHeader);