export function setWelcomeMessage(value){
    const cookie = new Cookies();
    cookie.set('TraineeWelcomeMessage', value);
}

export function getWelcomeMessage(){
    const cookie = new Cookies();
    if (cookie.get('TraineeWelcomeMessage') === undefined){
        return true
    }else if((cookie.get('TraineeWelcomeMessage') === false)) {
        return false;
    }else{
        return cookie.get('TraineeWelcomeMessage');
    }
    
}

