import React, { useState, useEffect } from 'react';
import { View, Text, Image, ImageBackground, TouchableOpacity } from 'react-native';

import DashboardHeader from './Components/DashboardHeader';
import { InfoModal } from './Modals/InfoModal';

import sharedStyles from '../Styles/Shared/style';

import { connect } from 'react-redux';

import { getWelcomeMessage, getGlobalInstitutionDetails } from './Globals/';
import Cookies from 'universal-cookie/es6';


import CreateOrderIcon from '../assets/icons/create_order.png';
import SettingsIcon from '../assets/icons/Settings.png';
import TraineeRequestsIcon from '../assets/icons/trainees_requests.png';
import SystemLogIcon from '../assets/icons/system_log.png';
import TraineeLogIcon from '../assets/icons/trainee.png';

import NotificationBellCircle from '../assets/icons/notification_info_circle.png';



const cookie = new Cookies();


export function Dashboard(props){


    const [modalVisible, setModalVisible] = useState(false);
    const [message, setMessage]           = useState("");  
    const [countRequest, setCountRequest] = useState(0);


    const createOrder = () => {
        props.setPath('CreateOrder');
    }

    const traineesRequests = () => {
        props.setPath('TraineeRequests');
    }

    const Manage = () => {
        props.setPath('Manage');
    }

    const SystemLog = () =>{
        props.setPath('Logs');
    }

    const TraineesLog = () => {
        props.setPath('TraineeLogs');
    }


    const updateDashboardDetails = async () =>{
        // console.log("am here")
        // var data = await JSON.stringify(props.state.data);
        var requestsCounter = 0;
        if(props.state.data !== undefined){
            for(var order = 0;  order < props.state.data.orders.length; order++){
                // requestsCounter = requestsCounter + props.state.data.orders[order]["orderRequirements"].length;
                for(var requirement = 0; requirement < props.state.data.orders[order]["orderRequirements"].length; requirement++){
                    if(props.state.data.orders[order]["orderRequirements"][requirement]["status"] === "pending"){
                        requestsCounter = requestsCounter + 1;
                    }
                }
            }
            setCountRequest(requestsCounter);
        }
        // if(props.state.data !== undefined  &&  data !== getGlobalInstitutionDetails()){
        //     setCountRequest(props.state.data.orders.length);
        // }
    }

    useEffect(()=>{
        updateDashboardDetails()
    });


    return(
        <View>
            {/* <View style={{width: '100%'}}>
                <DashboardHeader />
            </View> */}


            <View style={sharedStyles.dashboardView}>

                <View style={{width: "90%", flexGrow: 1, flexDirection: 'row', justifyContent: 'space-evenly'}}>

                    <View style={sharedStyles.textInputDashCardBackLayer}>
                        <TouchableOpacity style={sharedStyles.textInputDashCardFrontLayer} onPress={()=>{createOrder()}}>
                            <Image source={CreateOrderIcon} style={{width:50, height:50}} />
                            <Text style={sharedStyles.DashCardText}>إنـشـاء عـرض</Text>
                        </TouchableOpacity>
                    </View>


                    <View style={sharedStyles.textInputDashCardBackLayer}>
                        <TouchableOpacity style={sharedStyles.textInputDashCardFrontLayer} onPress={()=>{traineesRequests()}}>
                            <Image source={TraineeRequestsIcon} style={{width:50, height:50}} />
                            <Text style={sharedStyles.DashCardText}>طـلبات الـمتـدربـين</Text>
                            {
                                countRequest > 0 ?
                                    <ImageBackground source={NotificationBellCircle} style={sharedStyles.request_notification_circle}>
                                        <Text style={[sharedStyles.request_notification_circle_text, { fontSize: countRequest > 999 ? 12 : 18  }]}>{countRequest > 9999 ? '9999+' : countRequest}</Text>
                                    </ImageBackground>
                                :
                                    null
                            }
                        </TouchableOpacity>
                    </View>

                    <View style={sharedStyles.textInputDashCardBackLayer}>
                        <TouchableOpacity style={sharedStyles.textInputDashCardFrontLayer} onPress={()=>{Manage()}}>
                            <Image source={SettingsIcon} style={{width:50, height:50}} />
                            <Text style={sharedStyles.DashCardText}>إدارة</Text>
                        </TouchableOpacity>
                    </View>


                </View>

                <View style={{width: "90%", flexGrow: 1, flexDirection: 'row', justifyContent: 'space-evenly', marginLeft: 50, marginRight: 50, paddingTop: 10}}>

                    <View style={sharedStyles.textInputDashCardBackLayer}>
                        <TouchableOpacity style={sharedStyles.textInputDashCardFrontLayer} onPress={()=>{SystemLog()}}>
                            <Image source={SystemLogIcon} style={{width:50, height:50}} />
                            <Text style={sharedStyles.DashCardText}>سـجـل النظام</Text>
                        </TouchableOpacity>
                    </View>

                    <View style={[sharedStyles.textInputDashCardBackLayer, {marginLeft: -20}]}>
                        <TouchableOpacity style={sharedStyles.textInputDashCardFrontLayer} onPress={()=>{TraineesLog()}}>
                            <Image source={TraineeLogIcon} style={{width:50, height:50}} />
                            <Text style={sharedStyles.DashCardText}>سـجـل الـمتـدربـين</Text>
                        </TouchableOpacity>
                    </View>

                </View>
            
            </View>


             <View>
                {
                    modalVisible === true ?
                        <InfoModal message={message} setModalVisible={setModalVisible} modalVisible={modalVisible} />
                    :
                        null
                    // <InfoModal setModalVisible={setModalVisible} modalVisible={modalVisible} />
                }
                
            </View> 
            
        </View>
    );
}


const mapStateToProps = (state) => {
    return{
        state:{
            data: state.broadcastNewInstitutionDetails.data
        }
    }
}

// const mapDispatchToProps = (dispatch) => {
//     return {
//         fetchNotifications: (username, token)=>{
//             dispatch(fetchNotifications(username, token))
//         }
//     }
// }


export default connect(mapStateToProps, null)(Dashboard);