
import React, {useState} from 'react';
import { Provider } from 'react-redux';
import { createStore, applyMiddleware } from 'redux';
import thunk from 'redux-thunk';
import rootReducer from './Views/reducers';
import {Platform, View, Text, Animated} from 'react-native';
import {isMobile} from 'react-device-detect';

import Logo from './assets/images/trainee_ar.png';


import { NavigationContainer } from '@react-navigation/native';
import { createNativeStackNavigator } from '@react-navigation/native-stack';

import * as Font from 'expo-font';

import Login from './Views/Login';
import SignUp from './Views/SignUp';
import ForgotPassword1 from './Views/ForgotPassword1';
import ForgotPassword2 from './Views/ForgotPassword2';
import Dashboard from  './Views/Dashboard';
import Notifications from './Views/Components/Notifications';
import NotificationDetails from './Views/Components/NotificationDetails';
import CreateOrder from './Views/CreateOrder';
import Logs from './Views/Components/Logs';
import Manage from './Views/Components/Manage';
import BackArrow from './Views/Components/BackArrow';
import CreatedOrders from './Views/CreatedOrders';
import EditOrder from './Views/Components/EditOrder';
import Profile from './Views/Profile';
import TraineeRequests from './Views/Components/TraineeRequests';
import TraineeLogs from './Views/Components/TraineeLogs';
import DashboardHeader from './Views/Components/DashboardHeader';

import { Image } from 'react-native-web';


const customFonts        = {
  'AbdoMaster': require('./assets/fonts/AbdoMaster.ttf')
};

const Stack              = createNativeStackNavigator();

const store              = createStore(rootReducer, applyMiddleware(thunk));//composeWithDevTools()

const PathStack          = ["Login"];

const Navigation = () => {

  const [path, setURLPath]                = useState(PathStack[0]);
  const [backAnimation, setBackAnimation] = useState(false);

  const setPath = (path) => {
    // console.log("new path is " + path);
    PathStack.push(path);
    setURLPath(path);
  }

  const goBack = () => {
    if(PathStack.length > 1){
      var previous_path = PathStack[PathStack.length - 2];
      PathStack.pop();
      PathStack.pop();
      setPath(previous_path);
    }
  }


  const getPath = () => {
    return path;
  }

  const refresh = () => {
    var current_path = PathStack[PathStack.length - 1];
    PathStack.pop();
    setPath(current_path);
  }

  let props = {
    getPath: getPath,
    setPath: setPath,
    goBack: goBack,
    refresh: refresh
  };


  var ViewSlideIn = new Animated.ValueXY({x:-2000, y: 0});
  // var ViewSlideOut = new Animated.ValueXY({x:2000, y: 0});


  Animated.spring(ViewSlideIn, {
    toValue: {x: 0, y: 0},
    useNativeDriver: false,
    velocity: 1000,
    tension: 50,
  }).start();

  // Animated.spring(ViewSlideOut, {
  //   toValue: {x: 0, y: 0},
  //   useNativeDriver: false,
  // }).start();

  const dashboardContent = (view) => {
    switch (view){
      case 'Dashboard':
        return <Dashboard {...props} />

      case 'CreateOrder':
        return <CreateOrder {...props} />
      
      case 'Logs':
        return <Logs {...props}/>

      case 'Manage':
        return <Manage {...props}/>

      case 'Profile':
        return <Profile {...props}/>

      case 'CreatedOrders':
        return <CreatedOrders {...props}/>

      case 'TraineeRequests':
        return <TraineeRequests {...props}/>

      case 'TraineeLogs':
        return <TraineeLogs {...props}/>

      case 'Notifications':
        return <Notifications {...props}/>

      case 'NotificationDetails':
        return <NotificationDetails {...props}/>

      case 'EditOrder':
        return <EditOrder {...props}/>

      
      default:
        return (
            <View style={{alignSelf: 'center', alignItems: 'center', justifyContent: 'center'}}>
              <View style={{width: 120, marginRight: 20}}>
                    <BackArrow goBack={props.goBack} />
              </View>
              <Text style={{fontSize: 40, fontFamily: 'AbdoMaster'}}>
                لا توجد معلومات ٤٠٤
              </Text>
            </View>
          )
    }
  }

  switch (path){
    case 'Login':
      return (
        <Animated.View style={[{width: '100%', height: '100%'}, ViewSlideIn.getLayout()]}>
            <Login  {...props} />          
        </Animated.View>
    )

    case 'SignUp':
      return(
        <Animated.View style={[{width: '100%', height: '100%'}, ViewSlideIn.getLayout()]}>
            <SignUp {...props} />
        </Animated.View>
    )

    default:
      return(
        <Animated.View style={[{flexGrow: 1, flexDirection: 'column', justifyContent: 'flex-start'}, ViewSlideIn.getLayout()]}>
          <DashboardHeader {...props}  />
          <View>
            {dashboardContent(path)}
          </View>
        </Animated.View>
      )
  }
}


const Main = (props) => {
  Font.loadAsync(customFonts);

  return(
    <View style={{width: '100%', height: '100%'}}>
      {Navigation()}
    </View>
  );

}


const  Sorry = () => {
  Font.loadAsync(customFonts);

  return(

    <View style={{width: "100%", height: "100%", felx: 1, justifyContent: 'center', alignItems: 'center'}}>
        <Image source={Logo} style={{width: 400, height: 300, alignSelf: 'center'}} />
        <Text style={{fontFamily: 'AbdoMaster', textAlign: 'center', fontSize: 30, fontWeight: 'bold', color: '#EF8630'}}>
          عـذراً مـنـصـة مـتـدرب تـدعـم أجـهزة سـطح المكـتب فـقط
        </Text>
        <Text style={{fontFamily: 'AbdoMaster', textAlign: 'center', fontSize: 25, fontWeight: 'bold', color: 'red'}}>
           إذا كنت تمثل مؤسسـة يـمكنك التسجيل أو تسجيل الدخول عبر جهاز كمبيوتر مكتبي أو لابتوب 
        </Text>
        <Text style={{fontFamily: 'AbdoMaster', textAlign: 'center', fontSize: 25, fontWeight: 'bold', color: 'black'}}>
           أما إذا كنت متدرب وتبحـث جهة ما للتدريب فا يمكنك تحميل تطبيق متدرب من متجر التطبيقات الخاص بجهازك النقال
        </Text>

        <Text style={{fontFamily: 'AbdoMaster', marginTop: 30, textAlign: 'center', fontSize: 25, fontWeight: 'bold', color: '#EF8630'}}>
        مـتـدرب | 2022©
        </Text>
        <Text style={{fontFamily: 'AbdoMaster', marginTop: 5, textAlign: 'center', fontSize: 25, fontWeight: 'bold', color: '#EF8630'}}>
          Trainee-Platform جـمـيع الحقوق محفوظة لصالح منظمة 
        </Text>
    </View>

  );
}

export default function App (){
  return(
    <Provider store={store}>
      {
        isMobile ?
          <Sorry />
        :
          <Main />
      }
    </Provider>
  );
}